import React from "react";
import { Link } from "react-router-dom";
import { DocumentTextIcon, ArrowRightOnRectangleIcon, UserCircleIcon } from "@heroicons/react/24/outline";
import { useAuth } from "../contexts/AuthContext";
import DarkModeToggle from "./DarkModeToggle";

const Header = () => {
  const { user, logout } = useAuth();

  const handleLogout = async () => {
    try {
      await logout();
    } catch (error) {
      console.error(' :', error);
    }
  };

  return (
    <header className="bg-white dark:bg-dark-primary shadow-sm transition-colors duration-200">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/*  */}
          <Link to="/" className="flex items-center space-x-3">
            <div className="w-8 h-8 bg-blue-600 dark:bg-blue-500 rounded-lg flex items-center justify-center">
              <DocumentTextIcon className="h-5 w-5 text-white" />
            </div>
            <div>
              <h1 className="text-xl font-bold text-gray-900 dark:text-white">
                TITLE-NOMICS
              </h1>
              <p className="text-xs text-gray-500 dark:text-gray-400">
                AI  
              </p>
            </div>
          </Link>

          {/*   */}
          <div className="flex items-center space-x-4">
            {/*   */}
            {user && (
              <div className="flex items-center space-x-2 text-sm text-gray-700 dark:text-gray-300">
                <UserCircleIcon className="h-5 w-5" />
                <span>{user.email}</span>
                <span className="px-2 py-1 bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200 rounded-full text-xs">
                  {user.role === 'admin' ? '' : ''}
                </span>
              </div>
            )}
            
            {/*   */}
            <DarkModeToggle />
            
            {/*   */}
            {user && (
              <button
                onClick={handleLogout}
                className="flex items-center space-x-1 px-3 py-2 text-sm text-gray-700 dark:text-gray-300 hover:text-red-600 dark:hover:text-red-400 transition-colors duration-200"
                title=""
              >
                <ArrowRightOnRectangleIcon className="h-5 w-5" />
                <span></span>
              </button>
            )}
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
